import { AspectRatio, Box, Card, CardBody, CardHeader, Heading, Image, Stack, StackDivider, Text } from "@chakra-ui/react";
import SizedModal from "../../../../components/SizedModal/SizedModal";


export default function ImageModal({ isOpen, onOpen, onClose, selectedImage }) {
    const { idResponse, result, imageResultUrl } = selectedImage ?? {}
    return (
        <SizedModal isOpen={isOpen} onClose={onClose} onOpen={onOpen} zIndex={999999999} >
            <AspectRatio>
                <Image
                    // width="100%"
                    // overflow="hidden"
                    src={imageResultUrl}
                    alt="Imagen procesada"
                    textAlign={'center'}
                    lineHeight="600px"
                    fit={"fill"}
                />
            </AspectRatio>
            <Card>
                <CardHeader>
                    <Heading size='md'>Detalle de la imagen procesada</Heading>
                </CardHeader>

                <CardBody>
                    <Stack divider={<StackDivider />} spacing='4'>
                        <Box>
                            <Heading size='xs' >
                                Id de la Respuesta
                            </Heading>
                            <Text pt='2' fontSize='sm'>
                                {idResponse}
                            </Text>
                        </Box>
                        <Box>
                            <Heading size='xs' >
                                Resultado
                            </Heading>
                            <Text pt='2' fontSize='sm'>
                                {result}
                            </Text>
                        </Box>
                        <Box>
                            <Heading size='xs' >
                                Enlace de la Imagen
                            </Heading>
                            <Text pt='2' fontSize='sm'>
                                {imageResultUrl}
                            </Text>
                        </Box>
                    </Stack>
                </CardBody>
            </Card>
        </SizedModal>
    )
}